import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ContactUsComponent } from './contact-us/contact-us.component';
import { DataPrivacyComponent } from './data-privacy/data-privacy.component';
import { HelpLayoutComponent } from './help-layout/help-layout.component';
import { HelpListComponent } from './help-list/help-list.component';
import { HelpRulesPaymentComponent } from './help-rules-payment/help-rules-payment.component';
import { HelpUserAgreementComponent } from './help-user-agreement/help-user-agreement.component';
import { RulesAndPaymentComponent } from './rules-and-payment/rules-and-payment.component';
import { UserAgreementComponent } from './user-agreement/user-agreement.component';

const routes: Routes = [
  {
    path: '',
    component: HelpLayoutComponent,
    children: [
      {
        path: '',
        redirectTo: 'list',
        pathMatch: 'full'
      },
      {
        path: 'list',
        component: HelpListComponent
      },
      {
        path: 'data-privacy',
        component: DataPrivacyComponent
      },
      {
        path: 'user-agreement',
        component: UserAgreementComponent
      },
      {
        path: 'agreement',
        component: HelpUserAgreementComponent
      },
      {
        path: 'rules-payment',
        component: HelpRulesPaymentComponent
      },
      {
        path: 'rules',
        component: RulesAndPaymentComponent
      },
      {
        path: 'contactus',
        component: ContactUsComponent
      },
      {
        path: '**',
        redirectTo: 'list',
        pathMatch: 'full'
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HelpRoutingModule { }
