import { Component, OnInit } from '@angular/core';
import { SharedService } from 'src/app/shared/shared.service';

@Component({
  selector: 'app-help-user-agreement',
  templateUrl: './help-user-agreement.component.html',
  styleUrls: ['./help-user-agreement.component.scss']
})
export class HelpUserAgreementComponent implements OnInit {

  constructor(private sharedService: SharedService) { }

  ngOnInit(): void {
    if (this.sharedService.isLanguageChanged) {
      this.sharedService.isLanguageChanged = false;
      window.location.reload();
    }
  }

}
