import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { SharedService } from 'src/app/shared/shared.service';
import { AppConstants } from "../../core/app.constatnts";
import { ConfigConstants } from 'src/app/models/constants';

@Component({
  selector: 'app-rules-and-payment',
  templateUrl: './rules-and-payment.component.html',
  styleUrls: ['./rules-and-payment.component.scss']
})
export class RulesAndPaymentComponent implements OnInit {

  isAgree: boolean = false;
  showBackBtn: boolean = true;

  constructor(private router: Router, private sharedService: SharedService) {
  }


  ngOnInit(): void {
    if (this.sharedService.isLanguageChanged) {
      this.sharedService.isLanguageChanged = false;
      window.location.reload();
    }
    if ((localStorage.getItem('surveyType') == ConfigConstants.shareQrCode || localStorage.getItem('surveyType') == '') && localStorage.getItem('claim') == ConfigConstants.eachSurvey) {
      this.showBackBtn = false;
    } else {
      this.showBackBtn = true;
    }
  }

  openTermsOrRulesScreen(path: string) {
    const url = this.router.serializeUrl(
      this.router.createUrlTree([path])
    );
    window.open(url, '_blank');
  }

  openExternalLink(): void {
    window.open(AppConstants.TERMS_AND_CONDITIONS, '_blank');
  }

  onBackClick() {
    this.router.navigateByUrl('home/language');
  }

  onAgreeClick() {
    this.router.navigateByUrl('home/info');
    // if (this.isAgree) {
    //   this.router.navigateByUrl('home/info');
    // }
    // this.isAgree = !this.isAgree
  }

}
